.header
{
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;

    .logoContainer
    {
        height: 100%;
        width: 70px;
        padding: 25px;
    }

    .options{
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        .option{
            padding: 10px 15px;
            text-decoration: none;
            color: black;
            font-size: 30px;
            cursor: pointer;
        }
    }
}